.todo-app {
    position: relative;
    top: 75px;
    width: 300px;
    background-color: #ffffff17;
    margin: auto;
    box-shadow:  3px 5px 6px -2px #1E194D;
    border-radius: 5px;
    min-height: 430px;
}

.header h1 {
    margin: 0 20px;
    color: #cfe9ba;
}

.input {
    display: flex;
    align-items: stretch;
    height: 40px;
    margin: 10px 20px;
    width: 100%;
}

.input input {
    flex: 1;
    outline: none;
}

.input button {
    color: white;
    font-weight: bold;
    background-color: #7706d8;
    border: none;
}

ul {
    list-style: none;
    color: #cfe9ba;
    padding: 0;
    width: 100%;
}

.content-list li {
    display: flex;
    padding: 10px 0;
    margin: 0 20px;
    transition: 0.3s;
}

.content-list li:hover {
    color: white;
    background-color: #ffffff12;
    cursor: pointer;
}

.content-list li span {
    flex: 1;
}


.footer h3 {
    padding: 0px 20px 10px 20px;
    color: #cfe9ba;
}
