.music-player {
	position: absolute;
    margin: auto;
    top: 75px;
    left: 0;
    right: 0;
    width: 300px;
    background-color: #ffffff17;
    border-radius: 5px;
    min-height: 430px;
}

header {
	text-align: center;
	margin: 20px;
}

header h2 {
	margin: 0;
	color: #cfe9ba;
}

header .singer {
	color: #cfe9ba;
}

.content {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-bottom: 20px;
}

.content-cd {
	width: 50%;

}

.content-cd img {
	position: relative;
    left: -5px;
	border-radius: 50%;
    width: 100%;
    border: 5px solid #d6eebb4a;
}

.content-range {

}

.content-range__run {
}

.content-range {
  width: 100%; /* Width of the outside container */
}

/* The slider itself */
.content-range__run {
  -webkit-appearance: none;  /* Override default CSS styles */
  appearance: none;
  width: 100%; /* Full-width */
  height: 4px; /* Specified height */
  background: #d3d3d3; /* Grey background */
  outline: none; /* Remove outline */
  opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
  -webkit-transition: .2s; /* 0.2 seconds transition on hover */
  transition: opacity .2s;
}

/* Mouse-over effects */
.content-range__run:hover {
  opacity: 1; /* Fully shown on mouse-over */
}

/* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
.content-range__run::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default look */
  appearance: none;
  width: 12px; /* Set a specific slider handle width */
  height: 12px; /* Slider handle height */
  background: #437244; /* Green background */
  cursor: pointer; /* Cursor on hover */
  border-radius: 50%;
}

.content-range__run::-moz-range-thumb {
  width: 12px; /* Set a specific slider handle width */
  height: 12px; /* Slider handle height */
  background: #437244; /* Green background */
  cursor: pointer; /* Cursor on hover */
  border-radius: 50%;
}

footer {
	position: absolute;
    left: 0;
    right: 0;
    bottom: 50px;
}

footer .control {
	display: flex;
	justify-content: space-between;
	width: 75%;
	align-items: center;
	margin: auto;
}

.prev i, .next i {
	font-size: 30px;
	cursor: pointer;
	color: #d6eebb;
}

.play-pause i {
	font-size: 50px;
	cursor: pointer;
	color: #d6eebb;
}

.prev:hover i, .next:hover i, .play-pause:hover i {
	color: #9bad88;
	transition: 0.5s;
}