.nav-menu {
	position: absolute;
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    justify-content: center;
    margin: 0;
}

.nav-menu .active {
	background-color: #0097ff;
}

.nav-menu li {
    position: relative;
	background-color: #00baff;
    padding: 20px;
    width: 10%;
    height: 30px;
    color: white;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

a {
    display: flex;
    align-items:center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    text-decoration: none;
    color: white;
}